@charset "UTF-8";
.fl-if_Form {
  display: block;
  position: relative;
  background-color: #E6F7FE;
  max-width: 100vw;
  overflow: auto;
  width: 100%;
  height: 100%;
  font-family: Europa, Arial, sans-serif;
  font-size: 16px;
  padding: 0;
  box-sizing: border-box; }
  .fl-if_Form * {
    box-sizing: border-box; }

.fl-if_Form-questionsViewBox {
  display: block;
  width: 100%;
  max-height: calc(100% - 3rem);
  overflow: hidden; }

.fl-if_Form-questions {
  display: block;
  width: 100%;
  text-align: right;
  padding: 1px 0;
  will-change: transform;
  transition: transform .4s ease-in-out; }

.fl-if_FormField {
  transform: scale(0.98);
  margin: 1em 0;
  transition: all .3s ease-in-out;
  opacity: .2;
  pointer-events: none; }
  .fl-if_FormField:first-child {
    margin-top: 10vh; }
  .fl-if_FormField:last-child {
    margin-bottom: 10vh; }
  .fl-if_FormField--active {
    pointer-events: all;
    opacity: 1;
    transform: scale(1); }

.fl-if_FormField-legend {
  font-size: 24px;
  padding-left: 10vw;
  padding-right: 5vw;
  margin: 0;
  text-align: left; }

.fl-if_Text, .fl-if_Textarea {
  padding-top: 37px;
  padding-right: 5vw;
  padding-bottom: .4em;
  padding-left: 10vw; }

.fl-if_Text-input, .fl-if_Textarea-input {
  min-height: 4em;
  width: 100%;
  margin: 0;
  padding: 10px;
  font-family: Europa, Arial, sans-serif;
  font-size: 18px;
  background-color: transparent;
  border: 2px solid transparent;
  border-bottom-color: #424f66;
  border-radius: 5px;
  box-sizing: border-box;
  color: #434f69;
  display: inline-block;
  overflow-x: auto;
  overflow-y: auto;
  resize: none;
  white-space: pre-wrap;
  font-smoothing: subpixel-antialiased;
  user-select: text; }
  .fl-if_FormField--active .fl-if_Text-input, .fl-if_FormField--active .fl-if_Textarea-input {
    outline: 0;
    border: 2px dashed #AAAAAA; }

.fl-if_Text-okButtonContainer, .fl-if_Textarea-okButtonContainer {
  padding-left: 10vw;
  padding-right: 5vw;
  padding-top: 37px;
  text-align: center; }

@keyframes fadeIn {
  0% {
    transform: translateY(0.3em);
    opacity: 0; }
  100% {
    transform: translateY(0%);
    opacity: 1; } }

.fl-if_Text-okButton, .fl-if_Textarea-okButton {
  display: inline-block;
  box-sizing: border-box;
  border-radius: 3px;
  box-shadow: 0 2px 0 0 #137f7a;
  transition: .1s background-color linear;
  position: relative;
  color: white;
  background-color: #1aaba4;
  font-family: Europa, Arial, sans-serif;
  text-transform: none;
  font-size: 18px;
  letter-spacing: .05em;
  padding: 1em 2.5em;
  text-align: center;
  border: 0;
  transition: all .3s ease; }
  .fl-if_Text-okButton:active, .fl-if_Textarea-okButton:active {
    box-shadow: 0 1px 0 0 #137f7a;
    top: 1px; }
  .fl-if_Text-okButton:focus, .fl-if_Textarea-okButton:focus {
    outline: 0; }
  .fl-if_Text-okButton--completed, .fl-if_Textarea-okButton--completed {
    background-color: #00bcd4;
    padding-right: 3.5em; }
    .fl-if_Text-okButton--completed::after, .fl-if_Textarea-okButton--completed::after {
      content: '✓';
      font-weight: bold;
      font-size: 1.8em;
      line-height: .1em;
      display: inline-block;
      overflow: visible;
      position: absolute;
      right: .8em;
      top: .8em;
      animation: fadeIn 0.5s ease-out forwards; }

.fl-if_NavigationBar {
  display: block;
  position: relative;
  width: 100%;
  background-color: #E6F7FE;
  border-top: 2px solid #05587a;
  text-align: right;
  padding: 0.2em 5vw;
  height: 3rem; }

.fl-if_NavigationBar-button {
  display: inline-block;
  box-sizing: border-box;
  border-radius: 3px;
  box-shadow: 0 2px 0 0 #137f7a;
  transition: .1s background-color linear;
  position: relative;
  color: white;
  background-color: #1aaba4;
  font-family: Europa, Arial, sans-serif;
  text-transform: none;
  font-size: 18px;
  letter-spacing: .05em;
  padding: 1em 2.5em;
  text-align: center;
  border: 0;
  font-size: 16px;
  padding: .5em 1em;
  margin: 0 .2em; }
  .fl-if_NavigationBar-button:active {
    box-shadow: 0 1px 0 0 #137f7a;
    top: 1px; }
  .fl-if_NavigationBar-button:focus {
    outline: 0; }
