.fl-if_Text {
  padding-top: 37px;
  padding-right: $s-right-padding;
  padding-bottom: .4em;
  padding-left: $s-left-padding;
}

.fl-if_Text-input {
  min-height: 4em;
  width: 100%;
  margin: 0;
  padding: 10px;

  font-family: $f-main;
  font-size: $s-font-medium;

  background-color: transparent;
  border: 2px solid transparent;
  border-bottom-color: $c-secondary;
  border-radius: 5px;

  box-sizing: border-box;
  color: $c-primary-text;
  display: inline-block;

  overflow-x: auto;
  overflow-y: auto;
  resize: none;
  white-space: pre-wrap;
  font-smoothing: subpixel-antialiased;
  user-select: text;

  .fl-if_FormField--active & {
    outline: 0;
    border: 2px dashed $c-secondary-text;
  }
}

.fl-if_Text-okButtonContainer {
  padding-left: $s-left-padding;
  padding-right: $s-right-padding;
  padding-top: 37px;
  text-align: center;
}

@keyframes fadeIn {
  0% { transform: translateY(.3em); opacity: 0;}
  100% {transform: translateY(0%); opacity: 1;}
}

.fl-if_Text-okButton {
  @include fl-if-button();

  transition: all .3s ease;

  // The transition to all of these properties in part
  // of the completed animation
  &--completed {
    background-color: $c-valid;
    padding-right: 3.5em;

    &::after {
      content: '✓';
      font-weight: bold;
      font-size: 1.8em;
      line-height: .1em;
      display: inline-block;
      overflow: visible;

      position: absolute;
      right: .8em;
      top: .8em;
      animation: fadeIn.5s ease-out forwards;
    }
  }
}
